@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #7f8c8d;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: 'Poppins', sans-serif;
}
ul{ list-style: none; padding: 0;}
.list-container {
  display: flex;
  justify-content: flex-start;
  position: relative;
  margin-bottom: 5px;
}
.list-container span {
  background: #ccc;
  color: #fff;
  width: 25px;
  height: 25px;
  line-height: 25px;
  text-align: center;
  border-radius: 50px;
  margin-left:25px;
  transform: rotate(45deg);
  position: absolute;
  right: 18px;
  font-size: 22px;
  cursor: pointer;
  opacity: 0.5;
}
.list-container span:hover{
  background: #e74c3c;
  opacity: 1;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@media screen and (max-width: 767px) { 
  input[type=text]{ width: 200px;}
  button{ width: 30px; height: 30px;font-size: 32px;}
} 

.main-div {
  background: #fff;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  border-radius: 12px;
  min-width: 300px;
  text-align: center;
}
h1{
    text-align: center;
    color: #fff;
    background: #9b59b6;
    margin-top: 0;
    border-top-left-radius: 12px;
    border-top-right-radius: 12px;
    font-weight: 100;
}
input[type="text"] {
  border: 0;
  border-bottom: 1px solid #9b59b6;
  width: 250px;
  margin-left: 15px;
  margin-right: 15px;
  padding: 5px 0px;
  font-size: 16px;
  color: #9b59b6;
  font-weight: 600;
  text-align: center;
}
input[type="text"]:focus,
button:focus{  
outline: 0;
}

button{
    border: none;
    background: #9b59b6;
    color: #fff;
    border-radius: 50px;
    width: 40px;
    height: 40px;
    font-size: 42px;
    text-align: center;
    line-height: 0;
    margin-right: 10px;
    cursor: pointer;
    box-shadow: 0px 3px 8px 2px #9e9e9e;
}
button:hover{
  background-color: #1abc9c;
}
